.toolbar .view-toggle .view-button {
  transition: all 0.2s ease;
}
.toolbar .view-toggle .view-button.active {
  color: #2563eb;
  background-color: #eff6ff;
}
.toolbar .view-toggle .view-button:not(.active):hover {
  background-color: #f3f4f6;
}
.toolbar .action-button {
  transition: all 0.2s ease;
}
.toolbar .action-button:hover {
  transform: translateY(-1px);
}
.toolbar .action-button.danger:hover {
  background-color: #fef2f2;
  color: #dc2626;
}
.toolbar .action-button.primary:hover {
  background-color: #eff6ff;
  color: #2563eb;
}
.toolbar .action-button.success:hover {
  background-color: #f0fdf4;
  color: #16a34a;
}
.toolbar .divider {
  background-color: #d1d5db;
}
.toolbar .sort-select {
  transition: border-color 0.2s ease;
}
.toolbar .sort-select:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.toolbar .bulk-actions {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
